export default {
	name: 'avatar',
	data() {
		return {
			imgList: [],
			size: 0,
			files: {}
		}
	},
	props: {
		updata:0,
		broadcast:{},
		flows:{}
	},
	created() {
		this.imgList=[];
		//console.log(this.flows);
		
	},
	methods: {
		fileClick() {
			document.getElementById('upload_file').click()
		},
		commitInfo() {
			var imgList = this.imgList;
			var images = new Array();
			for(var i = 0; i < imgList.length; i++) {
				images[i] = imgList[i].file.src;
			}
			let params = {
				bid:this.broadcast.bid,
				pro_id: this.broadcast.pid,
				f_id: this.broadcast.fid,
				uid: this.broadcast.uid,
				content: this.broadcast.content,
				image: images
			}
			console.log(params);
			var url="";
			if(this.updata==0){
				url=this.umcons.serviceAddress.CREATE_BROADCAST;
			}else{
				url=this.umcons.serviceAddress.EDIT_BROADCAST;
			}
			this.$axios.post(url, params).then(function(response) {
				var error = response.data.error;
				if(error == 0) {
					this.util.showAlert(response.data.mes);
				} else {
					this.util.showAlert("加载失败");
				}
				this.$emit('imagecall', false);
			}.bind(this));

		},commitExit(){
			this.$emit('imagecall', false);
		},
		fileChange(el) {
			if(!el.target.files[0].size) return;
			this.fileList(el.target.files);
			el.target.value = '';

		},
		fileList(files) {

			for(let i = 0; i < files.length; i++) {
				this.fileAdd(files[i]);
			}

		},
		fileAdd(file) {
			this.size = this.size + file.size; //总大小 
			let reader = new FileReader();
			reader.vue = this;
			reader.readAsDataURL(file);
			reader.onload = function() {
				file.src = this.result;
				this.vue.imgList.push({
					file
				});

			}

		},
		fileDel(index) {
			this.size = this.size - this.imgList[index].file.size;
			//总大小 
			this.imgList.splice(index, 1);
		},
		bytesToSize(bytes) {
			if(bytes === 0) return '0 B';
			let k = 1000, // or 1024 
				sizes = ['B', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'],
				i = Math.floor(Math.log(bytes) / Math.log(k));
			return(bytes / Math.pow(k, i)).toPrecision(3) + ' ' + sizes[i];
		},
		dragenter(el) {
			el.stopPropagation();
			el.preventDefault();
		},
		dragover(el) {
			el.stopPropagation();
			el.preventDefault();
		},
		drop(el) {
			el.stopPropagation();
			el.preventDefault();
			this.fileList(el.dataTransfer.files);
		},
		changeType: function(id) {
			this.broadcast.fid = id;
			console.log(id);
		},
		onSubmit() {
			return false;
		}

	}
}



