
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Panel from 'vux/src/components/panel/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import Alert from 'vux/src/components/alert/index.vue'

import appfooter from "../element/footer.vue"
import letterbar from "../element/letterbar.vue"
import gril from '../../../static/gril.png';
import boy from '../../assets/userphoto.jpeg';
export default {
name: 'page',
components: {
appfooter,
XHeader,
Group,
Panel,
AlertModule,
Alert,
letterbar
},
data() {
return {
msg: 'Welcome to Your Vue.js App',
title: "通讯录",
list: [],
uid: 2,
cid:0,
isShowHeader: true,
photo:boy
}
},
created() {
this.cid = this.util.getCid();
if(this.cid == null || this.cid == undefined) {
this.cid = 2;
}
this.onLoadStaff();

this.isShowHeader = !this.util.isWinXin();
},
methods: { //初始化事件
onImgError(item, $event) {
console.log(item, $event)
},
onLetterBack(tag){
console.log("收到字母："+tag)
},
onLoadStaff: function() {
let params = {
cid: this.cid
}
this.$axios.post(this.umcons.serviceAddress.GET_STAFF, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
var data = response.data.data;
this.list = data;
console.log(data);
} else {
this.showAlert("数据加载失败");
}

}.bind(this));
},
getItem: function(itemData) {
var item = {
src: boy,
title: '沙和尚',
desc: '18815575555',
url: '',
};
item.title = itemData.name;
item.desc = itemData.username;
item.url = "tel:" + itemData.username;
return item;
},
showAlert: function(msg) {
AlertModule.show({
title: '提示',
content: msg,
onShow() {
console.log('Module: I\'m showing')
},
onHide() {
console.log('Module: I\'m hiding now')
}
})

}
}
}



