
import AlertModule from 'vux/src/plugins/alert/module'


function getCid() {
let cid = localStorage.getItem("cid");

return cid;
}

function getUid() {
let uid = localStorage.getItem("uid");

return uid;
}

function toastAlert(msg) {
AlertModule.show({
title: '提示',
content: msg,
onShow() {
console.log('Module: I\'m showing')
},
onHide() {

}
})

}

function onNextBack() {


document.addEventListener('plusready', function() {
var webview = plus.webview.currentWebview();
plus.key.addEventListener('backbutton', function() {
webview.canBack(function(e) {
if(e.canBack) {
webview.back();
} else {



var first = null;
plus.key.addEventListener('backbutton', function() {

if(!first) {
first = new Date().getTime();
console.log('再按一次退出应用');
setTimeout(function() {
first = null;
}, 1000);
} else {
if(new Date().getTime() - first < 1500) {
plus.runtime.quit();
}
}
}, false);
}
})
});
});

}

function showAlert(msg) {
AlertModule.show({
title: '提示',
content: msg,
onShow() {
console.log('Module: I\'m showing')
},
onHide() {
console.log('Module: I\'m hiding now')
}
})

}

function getNowFormatDate() {
var date = new Date();
var seperator1 = "-";
var year = date.getFullYear();
var month = date.getMonth() + 1;
var strDate = date.getDate();
if(month >= 1 && month <= 9) {
month = "0" + month;
}
if(strDate >= 0 && strDate <= 9) {
strDate = "0" + strDate;
}
var currentdate = year + seperator1 + month + seperator1 + strDate + " 00:00";
return currentdate;
}

function getTodayDate() {
var date = new Date();
var seperator1 = "-";
var seperator2 = ":";
var year = date.getFullYear();
var month = date.getMonth() + 1;
var strDate = date.getDate();
var hhstr = date.getHours();
var mmStr = date.getMinutes();
var ssStr = date.getSeconds();
if(month >= 1 && month <= 9) {
month = "0" + month;
}
if(strDate >= 0 && strDate <= 9) {
strDate = "0" + strDate;
}
if(hhstr >= 0 && hhstr <= 9) {
hhstr = "0" + hhstr;
}
if(mmStr >= 0 && mmStr <= 9) {
mmStr = "0" + mmStr;
}
if(ssStr >= 0 && ssStr <= 9) {
ssStr = "0" + ssStr;
}
var currentdate = year + seperator1 + month + seperator1 + strDate + " " + hhstr + seperator2 + mmStr + seperator2 + ssStr;
return currentdate;
}

function isWinXin() {
var isWeiXin = navigator.userAgent.toLowerCase().indexOf('micromessenger') > -1 ? true : false;
return isWeiXin;
}
export default {
getCid,
getUid,
showAlert,
getNowFormatDate,
isWinXin,
toastAlert,
getTodayDate,
onNextBack

}



