export default {
	name:"letterbar",
	components: {
		

	},
	data() {
		return {

			letterarray: []

		}
	},
	props: ["page"],
	created() {
		var letterarray = new Array();
		for(var i = 0; i < 26; i++) {
			letterarray[i] = String.fromCharCode(65 + i);
		}
		this.letterarray = letterarray;
	},
	methods: { //初始化事件
		letterClick: function(arg) {
			this.$emit('onletter', arg);
			console.log(arg);
		}
	}
}



