
import photo from '../../../static/userphoto.jpeg';
import XHeader from 'vux/src/components/x-header/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Search from 'vux/src/components/search/index.vue'

const pulldownDefaultConfig = {
content: '下拉刷新',
height: 40,
autoRefresh: false,
downContent: '下拉刷新',
upContent: '释放后刷新',
loadingContent: '正在刷新...',
clsPrefix: 'xs-plugin-pulldown-'
}
const pullupDefaultConfig = {
content: '上拉加载更多',
pullUpHeight: 60,
height: 40,
autoRefresh: false,
downContent: '释放后加载',
upContent: '上拉加载更多',
loadingContent: '加载中...',
clsPrefix: 'xs-plugin-pullup-'
}
export default {
name: 'page',
components: {
XHeader,
Scroller,
Search
},
data() {
return {
title: "我的客户",
type: '1',
defaultImg: 'this.src="' + require('../../assets/userphoto.jpeg') + '"',
prolist: [{
title: '沙悟净',
time: "重要客户",
list: [{
label: '客户状态：',
value: '初步达成意向'
}, {
label: '负责人：',
value: '王经理'
}, {
label: '成交总额：',
value: '￥20W'
}, {
label: '最近联系：',
value: '2018-08-11'
}]
}],
uid: 0,
pullupDefaultConfig: pullupDefaultConfig,
pulldownDefaultConfig: pulldownDefaultConfig,
isPullup: true,
isPulldown: true,
value: '',
isLoad: false
}
},
created() {
this.uid = this.util.getUid();

if(this.uid == null || this.uid == undefined) {
this.uid = 33;
}
this.onLoadPro("");
},
methods: {
backpage:function(){
this.$router.go(-1);
console.log("调用成功")
},

onLoadPro: function(word) {
if(!this.isLoad) {
this.isLoad = true;
let params = {
uid: this.uid,
condition: word
}
console.log(params);
this.$axios.post(this.umcons.serviceAddress.GET_CUSTOMER, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
this.prolist = response.data.data;
console.log(response.data.data);
} else {
this.util.showAlert("加载失败");
}
this.isLoad = false;

}.bind(this));
}
},
getInfo: function(msg) {
if(msg != undefined) {
return msg;
} else {
return "暂无数据"
}
},
getPhoto: function(msg) {
if(msg != undefined) {
return msg;
} else {
return photo;
}
},
openClientFlow: function(id) {
this.$router.push({
name: 'AddClient',
params: {
uid: id
}
});
},
pulldownLoading() {
console.log("下拉刷新");
var that = this;
setTimeout(function() {
that.onLoadPro("");
that.$refs.mScroller.donePulldown();
}, 1000);

},
pullupLoading() {

if(this.isPullup) {
this.isPullup = false;
var that = this;
that.$refs.mScroller.donePullup()
that.$refs.mScroller.disablePullup();
console.log("上拉加载")
setTimeout(function() {
that.$refs.mScroller.enablePullup();
that.isPullup = true;
}, 1000);
}

},
onSubmit() {
this.$refs.search.setBlur()
/*this.$vux.toast.show({
type: 'text',
position: 'top',
text: 'on submit'
})*/
},
onFocus() {
console.log('on focus')
},
onCancel() {
console.log('on cancel')
},
resultClick(item) {
window.alert('you click the result item: ' + JSON.stringify(item))
},
getResult(val) {
console.log('on-change', val);
if(val != undefined && val.length > 0) {

setTimeout(this.onLoadPro(val), 500);

}
}
}
}

function getResult(val) {
let rs = []
for(let i = 0; i < 5; i++) {
rs.push({
title: "项目" + i,
other: i
})
}
return rs
}



