
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import Alert from 'vux/src/components/alert/index.vue'

export default {
name: 'page',
components: {
XInput,
Group,
XButton,
Cell,
AlertModule,
Alert
},
data() {
return {
title: "工作台",
results: [],
value: '',
phone:"",
password:""
}
},
created() {

},
methods: { //初始化事件
onLogin: function() {

if(this.phone.length!=11){
this.showAlert("请输入手机号");
return;
}
if(this.password.length<6){
this.showAlert("请输入密码");
return;
}
let params = {
phone: this.phone,
password:this.password,
type:1
}

this.$axios.post(this.umcons.serviceAddress.LOGIN, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
var data=response.data.data;
this.umcons.userinfo=data;
localStorage.setItem("cid",data.cid);
localStorage.setItem("uid",data.id);
this.$router.push({ name: 'Home', params:{ cid: data.cid}});
} else {
this.showAlert("加载失败");
}

}.bind(this));

},
onPush:function(cid){

},
showAlert: function(msg) {
AlertModule.show({
title: '提示',
content: msg,
onShow() {
console.log('Module: I\'m showing')
},
onHide() {
console.log('Module: I\'m hiding now')
}
})

}
}
}



