
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Cell from 'vux/src/components/cell/index.vue'

import VChart from 'vux/src/components/v-chart/v-chart.vue'
import VLine from 'vux/src/components/v-chart/v-line.vue'
import VPoint from 'vux/src/components/v-chart/v-point.vue'
import VScale from 'vux/src/components/v-chart/v-scale.vue'
import VTooltip from 'vux/src/components/v-chart/v-tooltip.vue'

import appfooter from "../element/footer.vue"

export default {
name: 'page',
components: {
appfooter,
XHeader,
Group,
Grid,
GridItem,
VChart,
VPoint,
VLine,
VScale,
VTooltip,
Cell
},
data() {
return {
msg: 'Welcome to Your Vue.js App',
title: "统计",
isShowHeader: true,
cid: 2,
uid: 0,
own: {
"projectCount": 1,
"notStartCount": 0,
"onStartCount": 1,
"endtStartCount": 0
},
company: {
"comeraCount": 11,
"projectCount": 10,
"staffCount": 52,
"userCount": 71,
"invitation": 1015
},

datas: [{
time: '2016-08-08',
tem: 6
},
{
time: '2016-08-09',
tem: 22
},
{
time: '2016-08-10',
tem: 10
},
{
time: '2016-08-11',
tem: 26
},
{
time: '2016-08-12',
tem: 20
},
{
time: '2016-08-13',
tem: 1
},
{
time: '2016-08-10',
tem: 3
},
{
time: '2016-08-10 02:00:00',
tem: 20
},
{
time: '2016-08-10 02:20:00',
tem: 18
}
]
}
},
created() {
this.isShowHeader = !this.util.isWinXin();
this.cid = this.util.getCid();
this.uid = this.util.getUid();
if(this.cid == null || this.cid == undefined) {
this.cid = 2;
}

this.initPro();
},
methods: { //初始化事件
initPro: function() {

let params = {
cid: this.cid,
uid: this.uid
}
console.log(params);
this.$axios.post(this.umcons.serviceAddress.COMPANY_RECORD, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
var data = response.data.data;
this.company = data.companydata;
this.own= data.own;
var alive = data.companydata.alive;

var datas = new Array();
for(var i = 0; i < alive.length; i++) {
datas[i] = {
time: alive[i].day,
tem: alive[i].count
}

}
this.datas = datas;
console.log(datas)
} else {
this.util.showAlert("加载失败");
}

}.bind(this));

}
}
}



